<script>
import Vue from "vue";
import Header from "@/components/Search/Header.vue";
import Tabber from "@/components/common/Tabber.vue";
import BScroll from "better-scroll"; //页面滚动插件
import { getShopList } from "@/api/home.js";
import { Lazyload } from "mint-ui";
Vue.use(Lazyload);
export default {
  name: "search-list",
  components: {
    Header,
    Tabber,
  },
  data() {
    return {
      searchList: {
        currentIndex: 0, //判断当前处于那个索引值
        data: [
          /*status代表当前的状态值，如果status为0则都不亮，
          如果为1则上箭头亮，为2则下箭头亮*/
          { name: "综合", key: "zohe" },
          { name: "价格", status: 0, key: "price" },
          { name: "销量", status: 0, key: "num" },
        ],
      },
      shopList: [],
      shopName: JSON.parse(localStorage.getItem("searchList"))[0],
      noShopList: false,
    };
  },
  created() {
    this.shopListData();
  },
  mounted() {
    this.$refs.header.$on("headerSearchShopList", this.headerSearchShopList);
  },
  computed: {
    orderBy() {
      //知道当前是哪一个对象
      let obj = this.searchList.data[this.searchList.currentIndex];
      //针对于状态，判断是升序还是降序
      let val = obj.status == 1 ? "asc" : "desc";
      return {
        [obj.key]: val,
      };
    },
  },
  methods: {
    //调用查找元素列表
    async shopListData(name) {
      let res = null;
      if (name) {
        res = await getShopList(name, this.orderBy);
      } else {
        res = await getShopList(this.shopName, this.orderBy);
      }
      if (res.data.code == 200) {
        this.shopList = res.data.data;
      }
      if (this.shopList.length == 0) {
        this.noShopList = true;
      } else {
        this.noShopList = false;
      }
      this.$nextTick(() => {
        new BScroll(this.$refs.wrapper, {
          movable: true,
          zoom: true,
        });
      });
    },
    //header中触发查找元素列表
    headerSearchShopList(name) {
      this.shopListData(name);
    },
    //tab中的点击事件
    changeTab(index) {
      this.searchList.currentIndex = index;
      let item = this.searchList.data[index];
      this.searchList.data.forEach((v, i) => {
        if (i != index) {
          v.status = 0;
        }
      });
      if (index == this.searchList.currentIndex) {
        item.status = item.status == 1 ? 2 : 1;
      }
      this.shopListData();
    },
  },
};
</script>

