<template>
  <header>
    <div class="search-return" @click="goBackPage">
      <i class="iconfont icon-fanhui"></i>
    </div>
    <div class="search-main">
      <i class="iconfont icon-fangdajing"></i>
      <form action="" onsubmit="return false" @keyup.enter="goSearchList">
        <input
          type="text"
          placeholder="搜索您喜欢的商品..."
          v-model="searchVal"
          style="font-size: 0.4rem; color: #333"
          ref="myInput"
        />
      </form>
    </div>
    <div class="search-btn" @click="goSearchList">搜索</div>
  </header>
</template>

<script>
export default {
  name: "search-header",
  data() {
    return {
      searchVal: "",
      searchArr: [],
    };
  },
  mounted() {
    window.addEventListener("touchmove", this.myTouchMove, true); //监听键盘事件
  },
  methods: {
    //让键盘失去焦点
    myTouchMove() {
      this.$refs.myInput.blur();
    },
    goBackPage() {
      this.$router.back();
    },
    goSearchList() {
      if (this.searchVal.trim() != "") {
        if (localStorage.getItem("searchList") == null) {
          localStorage.setItem("searchList", "[]");
        } else {
          this.searchArr = JSON.parse(localStorage.getItem("searchList"));
        }
        this.searchArr.unshift(this.searchVal);
        this.searchArr = Array.from(new Set(this.searchArr));
        localStorage.setItem("searchList", JSON.stringify(this.searchArr));

        if (this.$route.name != "searchList") {
          //路径如果没有变化则不进行页面跳转
          this.$router.push({
            name: "searchList",
            query: {
              key: this.searchVal,
            },
          });
        } else {
          this.$emit("headerSearchShopList", this.searchVal);
        }
      } else {
        return;
      }
    },
  },
};
</script>

<style scoped>
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 1.174rem;
  color: #fff;
  background-color: #b0352f;
}
.search-return,
.search-btn {
  padding: 0 0.3rem;
}
.search-return i {
  font-size: 0.75rem;
}
.search-main {
  display: flex;
  align-items: center;
  width: 6.9rem;
  height: 0.8rem;
  border-radius: 12px;
  background-color: #fff;
}
.search-main form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.search-main form input {
  width: 100%;
}
.search-main i {
  padding: 0 0.2rem;
  color: rgb(200, 200, 200);
}
.search-btn {
  font-size: 0.43rem;
}
</style>
